.container {
    width: 100vw;
    height: 100vh;
    position: absolute;
    top:0;
    bottom:0;
    left:0;
    right:0;
  }
  
  .resizeable {
    position: absolute;
    border: 2px solid #533535;
    width: 600px;
    height: 600px;
    border-radius: 3px;
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 15px;
    min-height: 15px;
    cursor: crosshair;
  }
  
  .resizer {
    position: absolute;
    background: black;
  }
  
  .resizer-r {
    cursor: col-resize;
    height: 100%;
    right: 0;
    top: 0;
    width: 5px;
  }
  
  .resizer-t {
    cursor: row-resize;
    height: 5px;
    left: 0;
    top: 0;
    width: 100%;
  }
  
  .resizer-b {
    cursor: row-resize;
    height: 5px;
    left: 0;
    bottom: 0;
    width: 100%;
  }
  
  .resizer-l {
    cursor: col-resize;
    height: 100%;
    left: 0;
    top: 0;
    width: 5px;
  }